                      <makeupCard :item=\"item\"></makeupCard>\r\n                        </li>\r\n                    </ul>\r\n                </div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n<script>\r\nimport Multiselect from \"vue-multiselect\";\r\nimport makeupCard from \"./commonComponents/makeupCard.vue\";\r\nimport searchHeader from \"./commonComponents/searchHeader.vue\";\r\nexport default {\r\n    components: {\r\n        Multiselect,\r\n        makeupCard,\r\n        searchHeader\r\n    },\r\n    props: [\"cities\", \"subcat\"],\r\n    data(){\r\n        return {\r\n            selectedVenue: null,\r\n            selectedCity: null,\r\n            cont: {},\r\n            ids: [\r\n                \"categories-slider\",\r\n                \"top-vendors\",\r\n                'popular-vendors',\r\n                \"featured-vendors\"\r\n\r\n            ],\r\n        }\r\n    },\r\n    methods:{\r\n        setSplider() {\r\n            let _this = this;\r\n            this.$nextTick(function () {\r\n                this.ids.forEach(function (item, index) {\r\n                    _this.cont[item] = new Splide(`#${item}`, {\r\n                        gap: 5,\r\n                        rewind: true,\r\n                        pagination: false,\r\n                        cover: true,\r\n                        // isNavigation: true,\r\n                        arrows: true,\r\n                        autoWidth: true,\r\n                        breakpoints: {\r\n                            567: {\r\n                                arrows: false,\r\n                                wheel: true,\r\n                                direction: \"ltr\",\r\n                                releaseWheel: false,\r\n                            },\r\n                        },\r\n                    });\r\n                    _this.cont[item].mount();\r\n                });\r\n            });\r\n        },\r\n        startLink(item){\r\n            if(item.path != null && item.path != ''){\r\n                if(item.parent_id == null){\r\n                    window.location.href = `/directory/vendors/${item.path}`;\r\n                }else{\r\n                    const filteredObject = this.subcat.filter(object => object.id == item?.parent_id); \r\n                    //console.log(filteredObject);\r\n                    window.location.href = `/directory/vendors/${filteredObject[0].path}/filtered?type=${item.slug}`;\r\n\r\n                }\r\n            }else{\r\n                window.location.href = `/directory/vendors/venues/filtered?type=${item.name}`;\r\n            } \r\n        }\r\n    },\r\n    mounted() {\r\n        document.onreadystatechange = () => {\r\n            if (document.readyState == \"complete\") {\r\n                this.setSplider();\r\n            }\r\n        };\r\n    },\r\n\r\n}\r\n</script>\r\n\r\n<style scoped>\r\n\r\n.vendor-cats-cont{\r\n    background: #F4F4F4;\r\n    padding: 54px 0;\r\n    position: relative;\r\n}\r\n.vendor-cats-cont > h1 {\r\n    color: #000;\r\nfont-family: Poppins;\r\nfont-size: 18px;\r\nfont-style: normal;\r\nfont-weight: 500;\r\nline-height: normal;\r\nmargin-bottom: 15px;\r\n}\r\n#categories-slider a {\r\n    text-decoration: none;\r\n    height: 100%;\r\n    display: flex;\r\n    width: 114.128px;\r\n    padding: 14.106px;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    justify-content: flex-start;\r\n    gap: 8.153px;\r\n    border-radius: 7.694px;\r\n    background: #FFF;\r\n    cursor: pointer;\r\n}\r\n\r\n#categories-slider span {\r\n    color: #000;\r\n    text-align: center;\r\n    font-family: Poppins;\r\n    font-size: 16.62px;\r\n    font-style: normal;\r\n    font-weight: 400;\r\n    line-height: normal;\r\n}\r\n.vendor-common-cont {\r\n    padding: 54px 0;\r\n}\r\n.vendor-common-cont > div:nth-child(1) {\r\n    display: flex;\r\n    justify-content: space-between;\r\n}\r\n.vendor-common-cont > div:nth-child(1) > h1 {\r\n    color: #000;\r\n    font-family: Poppins;\r\n    font-size: 18px;\r\n    font-style: normal;\r\n    font-weight: 500;\r\n    line-height: normal;\r\n    margin-bottom: 15px;\r\n}\r\n.vendor-common-cont > div:nth-child(1) > a {\r\n    display: block;\r\n    color: #461952;\r\n    font-family: Poppins;\r\n    font-size: 14.088px;\r\n    font-style: normal;\r\n    font-weight: 400;\r\n    line-height: normal;\r\n    text-decoration-line: underline;\r\n    margin-bottom: 15px;\r\n}\r\n\r\n</style>\r\n<style>\r\n.categories-slider .splide__arrow{\r\n    background:none !important;\r\n}\r\n.categories-slider .splide__arrow--prev {\r\n    position: absolute;\r\n    top: -28px;\r\n    right: 45px;\r\n    left: unset;\r\n}\r\n.categories-slider .splide__arrow--next {\r\n    position: absolute;\r\n    top: -28px;\r\n    right: 10px;\r\n    left: unset;\r\n}\r\n</style>"],"sourceRoot":""}]);
